<template>
    <div class="create">
        <div class="content">
            <h3></h3>
            <s-form ref="formInline" :model="formInline" :rules="ruleInline">
                <s-form-item label="">
                    <s-select v-model="board">
                        <s-option v-for="item in options" :key="item.value" :value="item.value"
                                  :label="item.label"></s-option>
                    </s-select>
                </s-form-item>
                <s-form-item prop="title" class="title">
                    <s-input type="text" v-model="formInline.title" placeholder="">
                    </s-input>
                </s-form-item>
                <s-form-item prop="editor" class="editor">
                    <textarea v-model="formInline.editor" id="markdownEditor"></textarea>
                </s-form-item>
                <s-form-item class="btn">
                    <s-button type="primary" @click="handleSubmit('formInline')"></s-button>
                </s-form-item>
            </s-form>

        </div>
        <div class="sideBarList">
            <SideBar>
                <template #header>Markdown </template>
                <template #content>
                    <p>### </p>
                    <p>****</p>
                    <p>`console.log('')`</p>
                    <p>```js\n code \n``` </p>
                    <p>[]()</p>
                    <p>![]()</p>
                    <p>
                        <a href="https://segmentfault.com/markdown" target="_blank">Markdown </a>
                    </p>
                </template>
            </SideBar>
        </div>

    </div>
</template>

<script>
    import SimpleMDE from "simplemde";
    import SideBar from "@/components/SideBar";


    export default {
        name: "Create",
        components: {SideBar},
        data() {
            return {
                board: "share",
                simplemde: null,
                options: [{
                    value: 'share',
                    label: ''
                }, {
                    value: 'ask',
                    label: ''
                }, {
                    value: 'job',
                    label: ''
                }],
                formInline: {
                    editor: "",
                    title: ""
                },
                ruleInline: {

                    title: [
                        {required: true, message: '', trigger: 'change'},
                        {type: 'string', min: 10, message: '', trigger: 'change'}
                    ],
                    editor: [
                        {required: true, message: '', trigger: 'change'},

                    ]
                }
            }
        },
        methods: {
            publish() {
                this.$message({
                    type: "error",
                    message: "cnode",
                    duration: 1000
                })
            },
            handleSubmit(name) {
                this.$refs[name].validate(this.publish, this.publish)
            }
        },

        mounted() {
            this.simplemde = new SimpleMDE({element: document.getElementById("markdownEditor")});
        }
    }
</script>

<style scoped>
    .create {
        display: flex;
        font-size: 14px;
    }

    .content {
        background-color: #fff;
        padding: 20px;
        flex: 1;
        width: 0;

    }

    .title, .editor,.btn {
        margin-left: -15px;
    }

    h3 {
        margin-bottom: 20px;
    }

    .sideBarList {
        width: 290px;
        margin-left: 20px;
    }

    p {
        padding: 5px;
    }

    a {
        color: #778087;
    }
    .btn {
        margin-top: 20px;
    }


</style>
